'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
// Import ikon yang diperlukan
import { 
  Menu, X, LogOut, Database, BookOpen, Cpu 
} from 'lucide-react';
import { supabase } from '@/lib/supabase';

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);
  const [user, setUser] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Sinkronisasi status autentikasi secara real-time
    const syncAuth = async (currentUser: any) => {
      setUser(currentUser ?? null);
      setLoading(false);
    };

    // Ambil status user saat mount
    supabase.auth.getUser().then(({ data: { user } }) => syncAuth(user));

    // Listener untuk perubahan status (Login/Logout)
    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      syncAuth(session?.user);
    });

    return () => subscription.unsubscribe();
  }, []);

  const handleSignOut = async () => {
    await supabase.auth.signOut();
    window.location.href = '/';
  };

  return (
    <nav className="border-b border-gray-100 py-6 px-8 bg-white/80 backdrop-blur-xl sticky top-0 z-[100] no-print text-black">
      <div className="max-w-7xl mx-auto flex justify-between items-center">
        
        {/* 1. LOGO */}
        <Link href="/" className="group flex items-center gap-3">
          <div className="w-12 h-12 bg-black rounded-xl flex items-center justify-center group-hover:bg-[#D4AF37] transition-all">
            <span className="text-white font-serif text-2xl italic font-bold group-hover:text-black">G</span>
          </div>
          <div className="text-2xl font-serif tracking-[0.2em] font-bold uppercase">
            Global<span className="text-[#D4AF37]">Tax</span>
            <span className="block text-[12px] tracking-[0.5em] text-gray-400 font-sans -mt-1 uppercase">Laboratory</span>
          </div>
        </Link>
        
        {/* 2. DESKTOP NAV - Font size disesuaikan ke standar 14px */}
        <div className="hidden md:flex items-center gap-10">
          <Link href="/encyclopedia" className="text-[14px] font-black uppercase tracking-widest text-gray-400 hover:text-black transition-colors flex items-center gap-2">
            <BookOpen size={16} className="text-[#D4AF37]" /> Encyclopedia
          </Link>

          <Link href="/intelligence" className="text-[14px] font-black uppercase tracking-widest text-gray-400 hover:text-black transition-colors flex items-center gap-2">
            <Database size={16} className="text-[#D4AF37]" /> Intelligence
          </Link>

          {/* AI TAX VAULT dengan Marker Emas (Gold Pill Design) */}
          <Link href="/ai-vault" className="bg-[#D4AF37] text-black px-6 py-2.5 rounded-full text-[14px] font-black uppercase tracking-widest flex items-center gap-2 hover:bg-black hover:text-[#D4AF37] transition-all shadow-lg shadow-gold/10">
            <Cpu size={16} /> AI Tax Vault
          </Link>
          
          {!loading && user && (
            <button onClick={handleSignOut} className="ml-4 p-2 text-gray-300 hover:text-red-500 transition-colors border-l border-gray-100 pl-6" title="Sign Out Session">
              <LogOut size={22} />
            </button>
          )}
        </div>

        {/* MOBILE TOGGLE */}
        <button className="md:hidden" onClick={() => setIsOpen(!isOpen)}>
          {isOpen ? <X size={28} /> : <Menu size={28} />}
        </button>
      </div>

      {/* 3. MOBILE MENU OVERLAY */}
      {isOpen && (
        <div className="absolute top-full left-0 w-full bg-white border-b border-gray-100 p-8 flex flex-col gap-8 md:hidden shadow-2xl animate-in slide-in-from-top duration-300">
          <div className="flex flex-col gap-8">
            <Link href="/encyclopedia" onClick={() => setIsOpen(false)} className="flex items-center gap-4 text-[14px] font-bold uppercase tracking-widest text-black">
              <BookOpen size={20} className="text-[#D4AF37]" /> Encyclopedia
            </Link>
            <Link href="/intelligence" onClick={() => setIsOpen(false)} className="flex items-center gap-4 text-[14px] font-bold uppercase tracking-widest text-black">
              <Database size={20} className="text-[#D4AF37]" /> Intelligence
            </Link>
            <Link href="/ai-vault" onClick={() => setIsOpen(false)} className="flex items-center gap-4 text-[14px] font-bold uppercase tracking-widest text-[#D4AF37] bg-black p-4 rounded-xl">
              <Cpu size={20} /> AI Tax Vault
            </Link>
            
            {user && (
              <button onClick={handleSignOut} className="w-full py-4 text-red-500 font-bold uppercase tracking-widest text-[12px] flex items-center gap-4 border-t border-gray-50 pt-8">
                <LogOut size={18} /> Sign Out Member Session
              </button>
            )}
          </div>
        </div>
      )}
    </nav>
  );
}